
body {
  margin: 0em;
  padding: 0em;
}
.page {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 3em;
}
@media only screen and (max-width: 768px) {
.page {
      margin-top: 4em;
}
}
.page .page-body {
    width: 60%;
    text-align: left;
    margin-bottom: 5em;
}
.page .page-body.small {
      width: 45%;
}
@media only screen and (max-width: 768px) {
.page .page-body {
        width: 100%;
}
}
.page .page-body ul {
      list-style: none;
      padding-left: 0em;
}
.page .page-body a {
      text-decoration: none;
}
.page .page-body a:visited {
        text-decoration: none;
        color: #000;
}
.content h2 {
  font-family: "soleil", sans-serif;
  font-weight: 900;
  font-size: 2.5rem;
  color: #e9ac00;
  margin: .4em 0em .1em 0em;
}
@media only screen and (max-width: 768px) {
.content h2 {
      font-size: 2.2rem;
}
}
.content h3 {
  font-family: "soleil", sans-serif;
  font-weight: 900;
  font-size: 1.5rem;
  color: #000;
  margin: .2em 0em;
}
.content h4 {
  font-family: "soleil", sans-serif;
  font-size: 1.5rem;
  font-weight: 400;
  color: #999;
  margin: 0em 0em .6em 0em;
}
@media only screen and (max-width: 768px) {
.content h4 {
      font-size: 1.25rem;
}
}
.content h5 {
  font-family: "soleil", sans-serif;
  font-size: 1.35rem;
  margin: 1.2em 0em .6em 0em;
}
@media only screen and (max-width: 768px) {
.content h5 {
      font-size: 1.1rem;
}
}
.content p {
  white-space: pre-wrap;
  font-family: "adobe-caslon-pro", serif;
  font-size: 1.25rem;
  margin: .2em 0em;
}
@media only screen and (max-width: 768px) {
.content p {
      font-size: 1rem;
}
}
.header {
  display: flex;
  align-items: flex-start;
  flex-direction: row;
  background: #f9f9f9;
  height: 200px;
  margin: 2em 0em;
}
@media only screen and (max-width: 768px) {
.header {
      height: 100px;
      margin: 1em;
}
}
.header .headshot {
    display: inline-block;
    height: 200px;
}
@media only screen and (max-width: 768px) {
.header .headshot {
        height: 100px;
}
}
.header .info {
    padding: 1.2em 2em;
}
@media only screen and (max-width: 768px) {
.header .info {
        padding: .5em 1em .5em 1em;
}
}
.header .info h3 {
      margin: .2em 0em;
      color: #333;
}
@media only screen and (max-width: 1024px) {
.header .info h3 {
          font-size: 1.2rem;
}
}
@media only screen and (max-width: 768px) {
.header .info h3 {
          font-size: 1rem;
}
}
.header .info h3 strong {
        color: #e9ac00;
}
.header .info p {
      margin: .1em 0em;
      font-size: 1.1rem;
      color: #333;
      line-height: 1.5;
}
@media only screen and (max-width: 1024px) {
.header .info p {
          font-size: .95rem;
}
}
@media only screen and (max-width: 768px) {
.header .info p {
          display: none;
}
}
.header .info .icon {
      height: 25px;
      margin: .5em .35em;
}
@media only screen and (max-width: 1024px) {
.header .info .icon {
          height: 20px;
}
}
.blogpost {
  padding: .5em 2em;
  border-radius: 5px;
  color: #333;
}
@media only screen and (max-width: 768px) {
.blogpost {
      padding: .5em 1em;
}
}
.blogpost .title {
    text-align: center;
}
.blogpost .title h2 {
      font-family: "adobe-caslon-pro", serif;
      font-size: 3rem;
      line-height: .5;
      margin-bottom: .25em;
      color: #333;
}
.blogpost .title h4 {
      font-family: "soleil", sans-serif;
      font-weight: 700;
      font-size: 2rem;
      color: #577b90;
}
.blogpost ul {
    margin: 0em;
    padding: 0em;
    list-style: none;
}
.blogpost ul li h5 {
      font-size: 1.4rem;
      text-align: center;
      margin-top: 2em;
}



